import * as React from 'react';
import './index.scss';
import { Alert, Modal, Tabs, Button, Icon, Select, Avatar, Row, Col, Drawer } from 'antd';
import Utils from '../../../utils/utils'
import allPuzzles from '../../../utils/puzzle_data';
const Puzzle = require('react-image-puzzle');

const PIC_NUM = 87;
const defaultImgNum = Math.floor(Math.random() * PIC_NUM);
const defaultImg = require(`../../../images/puzzle/${defaultImgNum}.jpg`);
const TabPane = Tabs.TabPane;
const Option = Select.Option;
const PuzzleImage = Puzzle.default;
const puzzles = allPuzzles;

interface IState {
    isPc: boolean,
    img: ImageData,
    imgNum: any,
    size: number,
    visible: boolean,
    finished: boolean
}

export default class MemberImagePuzzle extends React.Component<{}, IState>{
    private textInput: string;

    constructor(props: {}) {
        super(props);
        this.state = {
            isPc: Utils.isPc(),
            img: defaultImg,
            imgNum: defaultImgNum,
            size: 500,
            visible: false,
            finished: false
        };
        this.textInput = 'select';
    };

    public render() {
        const { isPc, img, imgNum, size, visible, finished } = this.state;
        const options = [];
        for (let i = 0; i < PIC_NUM; i++) {
            options.push(
                <Option key={i.toString()} value={i}>
                    <Avatar shape="square" size='small' src={
                        require(`../../../images/puzzle/${i}.jpg`)
                    } />
                </Option>
            )
        }
        if (isPc) {
            return (
                <div>
                    {
                        finished ?
                            (() =>
                                (<div>
                                    <Button type="primary" icon="search" style={{ marginRight: 5 }} onClick={this.checkOriginalImage}>查看原图</Button>
                                    <Drawer
                                        title="查看原图"
                                        placement="left"
                                        onClose={this.closeOriginalImage}
                                        visible={visible}
                                        width="50%"
                                        style={{ textAlign: 'center' }}
                                    >
                                        <Button type="primary" href={require(`../../../images/puzzle/${imgNum}.jpg`)} target="_blank" style={{ marginBottom: 10 }} >新页面打开原图</Button>
                                        <img style={{ width: '100%' }} src={require(`../../../images/puzzle/${imgNum}.jpg`)} />
                                    </Drawer>
                                    <Button type="danger" onClick={this.restartPuzzle}>拼图成功，点我重新开始</Button>
                                </div>)
                            )()
                            :
                            (() =>
                                (<Row>
                                    <Col span={22}>
                                        <Button type="primary" icon="search" style={{ marginRight: 5 }} onClick={this.checkOriginalImage}>查看原图</Button>
                                        <Drawer
                                            title="查看原图"
                                            placement="left"
                                            onClose={this.closeOriginalImage}
                                            visible={visible}
                                            width="50%"
                                            style={{ textAlign: 'center' }}
                                        >
                                            <Button type="primary" href={require(`../../../images/puzzle/${imgNum}.jpg`)} target="_blank" style={{ marginBottom: 10 }} >新页面打开原图</Button>
                                            <img style={{ width: '100%' }} src={require(`../../../images/puzzle/${imgNum}.jpg`)} />
                                        </Drawer>
                                        <Button type="primary" style={{ marginRight: 5 }} onClick={this.changePuzzle}>换一张</Button>
                                        <Button type="primary" style={{ marginRight: 5 }} onClick={() => this.scalePuzzle('plus')}><Icon type="zoom-in" /></Button>
                                        <Button type="primary" onClick={() => this.scalePuzzle('reduce')}><Icon type="zoom-out" /></Button>
                                    </Col>
                                    <Col span={2}>
                                        <Select ref={this.textInput} value={imgNum} placeholder='选择图片' style={{ width: 100 }} onChange={this.selectPuzzle}>
                                            {options}
                                        </Select>
                                    </Col>
                                </Row>)
                            )()
                    }
                    <Tabs
                        defaultActiveKey="1"
                        tabPosition='top'
                        style={{ minWidth: 700, height: size + 100, textAlign: 'center' }}
                    >
                        {
                            puzzles.map(puzzle => {
                                return (
                                    <TabPane tab={puzzle.tab} key={puzzle.key}>
                                        <div style={{ display: "flex", justifyContent: "center", alignItems: "center" }}>
                                            <PuzzleImage image={img} size={size} level={puzzle.level} onDone={this.onSuccess} />
                                        </div>
                                    </TabPane>)
                            })
                        }
                    </Tabs>
                </div>
            )
        } else {
            return (
                <Alert
                    message="吼猴附体！"
                    description="因本页面受吼猴傻气侵袭，暂不支持手机访问，请更换电脑访问或访问其他页面！"
                    type="error"
                    showIcon
                />
            )
        }
    };

    private checkOriginalImage = () => {
        this.setState({
            visible: true,
        })
    };

    private closeOriginalImage = () => {
        this.setState({
            visible: false,
        })
    };

    private changePuzzle = () => {
        const num = Math.floor(Math.random() * PIC_NUM);
        this.selectPuzzle(num)
    };

    private selectPuzzle = (val: string | number) => {
        this.setState({
            img: require(`../../../images/puzzle/${val}.jpg`),
            imgNum: val
        })
    };

    private scalePuzzle = (type: string) => {
        if (type === 'plus') {
            this.setState({
                size: this.state.size + 50
            })
        } else {
            this.setState({
                size: this.state.size - 50
            })
        }
    };

    private restartPuzzle = () => {
        window.location.reload()
    }

    private onSuccess = () => {
        Modal.confirm({
            title: '拼图完成！',
            content: '拼图完成，您和吼猴一样聪明哦~',
            okText: '确定',
            cancelText: '继续玩',
            onOk: () => {
                this.setState({
                    finished: true
                })
            },
            onCancel: () => {
                this.restartPuzzle()
            }
        })
    }
}


